import banner from '@/components/banner'
import productList from '@/components/productList'
import uniDrawer from '@/components/uni-drawer/uni-drawer'
import productCategory from '@/components/product-category/productCategory'
import interfaces from '@/utils/interfaces.js'
export default {
	components: {
		banner,
		productList,
		productCategory,
		uniDrawer
	},
	data() {
		return {
			filterIndex: '',
			showRight: false,
			pageSize: 6, //分页大小
			pageNum: 1, //页码
			type: "", //类型1 发布采购 2 发布销售 3 委托销售
			loadingText: "正在加载....",
			currentPage: '/pages/category/category',
			cate_id: "",
			defalutSort: "", //是否综合排序 0 否 1 是 如果为 1
			priceSort: "", //价格排序 asc 升序 desc 降序
			timeSort: "", //剩余时间排序 asc 升序 desc 降序
			reload:false,
			info: [{
					colorClass: 'uni-bg-red',
					url: '/static/images/cateimg01.png',
					content: '内容 A'
				},
				{
					colorClass: 'uni-bg-green',
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg',
					content: '内容 B'
				},
				{
					colorClass: 'uni-bg-blue',
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg',
					content: '内容 C'
				}
			], //轮播图片
			goodsList: [],
			firstImages: '',
			categoryList: [],
		}

	},
	methods: {
		//弹窗显示
		show(e) {
			if (e === 'left') {
				this.showLeft = true
			} else {
				this.showRight = true
			}
		},
		hide() {

			this.showLeft = false
			this.showRight = false
		},
		closeDrawer(e) {
			if (e === 'left') {
				this.showLeft = false
			} else {
				this.showRight = false
			}
		},
		loadData() {
			this.request({ //分类筛选
				url: interfaces.getCategroyData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: {
					data: {
						id: '',
					}
				},
				success: ((res) => {
					this.categoryList = res.data;
				})
			});
			let params = {
				data: {
					page_size: this.pageSize,
					page_index: this.pageNum,
					keyword: "",
					type: "",
					price_sort: this.priceSort,
					remain_time_sort: this.priceSort,
					cate_id: this.cate_id,
					is_defalut_sort: this.defalutsort
				}
			}
			this.request({ //分类产品列表
				url: interfaces.getNeedsData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: params,
				success: (res) => {
					console.log(res.data);
					
					if (res.code == 200) {
						let lists = res.data.list;	
						for(let i=0;i<lists.length;i++){//转数组
							let serviceData =lists[i].service_cnname !=null && lists[i].service_cnname.length?lists[i].service_cnname.split(','):'';
							lists[i].service_cnname = serviceData;
							let qualification = lists[i].qualification_icon !=null && lists[i].qualification_icon.length?lists[i].qualification_icon.split(','):'';
							lists[i].qualification_icon = qualification;
						}	
						if (lists.length < this.pageSize) {
							this.loadingText = "到底了";
						}
						if (lists.length > 0) {
							lists.forEach(item => {
								this.goodsList.push(item);
							})
						} else {
							this.loadingText = "到底了";
						}
					}
				}
			})

		},
		handleSelect(index) {
			this.filterIndex = index;
			if (index === 0) {
				this.defalutSort = this.defalutSort === 1 ? 0 : 1;
			} else {
				this.defalutSort = '';
			}
			if (index === 1 || index === 2) {
				switch (this.priceSort) {
					case 'asc':
						this.priceSort = 'desc';
						break;
					case 'desc':
						this.priceSort = 'asc';
						break;
					default:
						this.priceSort = 'desc';
						break;
				}
			}
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
		},
		updateValue(item) { //分类筛选
			this.cate_id = item.id;
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
			this.closeDrawer('right');
		},

	},
	onNavigationBarButtonTap(e) {
		this.showRight = !this.showRight
	},
	onBackPress() {
		if (this.showRight || this.showLeft) {
			this.hide()
			return true
		}
	},
	onLoad() {
		this.loadData();
	},
	onPullDownRefresh() {

		setTimeout(() => {
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
			uni.stopPullDownRefresh();
		}, 1000)
	},
	// 上拉加载
	onReachBottom() {
		//debugger
		this.pageNum++;

		this.loadData();
	}
}
